export const title = "BaseObject"
export const links = [
                    {title:"Retornar", link:"/gamid-pages/graphics"}
                    ]

export const content = 
`
BaseObject  a classe abstrata responsvel por realizar a heranas
de classes concretas que podem ser instanciadas e inseridas em um Canvas.
 Tambm  possvel criar uma classe concreta em cima de base Object. Para tal, 
 extenda de gamid.graphics.BaseObject, e defina as duas funes obrigatrias: 
 newPosition(dt) e update().
`

export const attributeList = [
    {attr: "mass", description: "Massa do objeto instanciado"},
    {attr: "height", description: "Altura do objeto instanciado"},
    {attr: "width", description: "Largura do objeto instanciado"},
    {attr: "x", description: "Posio do objeto no canvas no eixo X"},
    {attr: "y", description: "Posio do objeto no canvas no eixo Y"},
    {attr: "newPosition(dt)", description: "Funo que atualiza a posio do objeto no canvas"},
    {attr: "update()", description: "Funo que atualiza a representao do objeto no canvas"},

]


