export const title = "Audio"
export const links = []

export const content =
`
Audio  a biblioteca responsvel por atribuir som  objetos. Ela  encarregada
de realizar todos os mtodos descritos posteriormente, do nicio ao fim da construo
de um udio.
`

export const attributeList = [
    {attr: "play(path_do_som)", description: "String : Inicializa o udio requerido."},
    {attr: "pause(path_do_som)", description: "String : Pausa o udio requerido e quando der o play() novamente, volta aonde tinha pausado."},
    {attr: "loop(path_do_som)", description: "String : Faz com que o udio funcione em loop. Quando acabar o udio, inicia novamente."},
    {attr: "stop(path_do_som)", description: "String : Finaliza a msica."}
]

export const example =
`
let canvasInstance = new gamid.graphics.Canvas(800, 800, 'cyan');
let canvas = canvasInstance.element;

// Cria um objeto do tipo Quadrado com seus respectivos atributos
let base = new gamid.graphics.Square(100, 100, 350, 350, 1, 'black');

// Adiciona um ou mais tipo de controles via Keyboard ao objeto instanciado (base)
let baseController = new gamid.controller.Keyboard(base, [87, 83]);

// Adiciona uma ao  tecla 'w'.
keyboard.setOnKeyDown(87, function(){
		square.speedY = -500;
});

// Adiciona uma ao  tecla 's'.
keyboard.setOnKeyDown(83, function(){
		square.speedY = 500;
});

// Cria a lista de udios que deseja inserir no seu jogo.
// Pode estar em qualquer formato de udio suportado pelo navegador (wav, mp3 ...).
let audioList = [
    "caminho_da_musica_1",
]

// Chama o construtor do udio que deseja, passando o objeto e a lista de audios.
// O construtor ir adicionar e criar os udios dentro do seu jogo.
let audio = new gamid.audio.Audio(base, audioList);

canvasInstance.addObject(base);

// Utiliza da mtodo play() para iniciar o udio.
// Este mtodo no reproduz novamente o udio quando esgotado seu tempo.
audio.play("caminho_da_musica_1");

// Caso queira usar o udio em loop pode utilizar do mtodo loop().
// Este mtodo s ir parar de reproduzir o udio quando chamada o
// mtodo de pause("caminho_da_musica_1") ou ento de stop("caminho_da_musica_1").
audio.loop("caminho_da_musica_1");

document.body.appendChild(canvas);

`



