export const title = "afterCollision()"
export const links = [
                    {title:"Retornar", link:"/gamid-pages/physics"}
                    ]

export const content =
`
Essa funo  ativada aps um dado objeto colidir com outro objeto colidvel,
 ou seja, outro objeto com fsicas atribudas.  ativado apenas quando hasCollision
est setado como true.
`

export const example =
`
let canvasInstance = new gamid.graphics.Canvas(800, 800, 'purple');
let canvas = canvasInstance.element;

// Cria um objeto do tipo quadrado com seus respectivos atributos.
// Width, height, posio no eixo X, posio no eixo Y, massa e cor, respectivamente.
let square = new gamid.graphics.Square(50, 50, 0, 0, 50, 'blue');

// Adiciona a fsica de coliso de quadrado para o objeto criado anteriormente.
let squarecollision = new gamid.physics.SquareCollision(square);
squarecollision.hasCollision = true; // j  true como padro!

// Como parmetro padro, essa funo recebe o prprio objeto
// atribuido  squarecollision, alm do objeto em que ele colidiu.
squarecollision.afterCollision = function(mainObject, collidedObject){
    // Torna o objeto rosa ao encostar em outro objeto.
    mainObject.color = "pink";
}

`



