export const title = "collidableObjects"
export const links = [
                    {title:"Retornar", link:"/gamid-pages/graphics"}
                    ]

export const content =
`
collidableObjects mantm uma lista de objetos que foram decorados com fsica por alguma
herana da classe Physics, encontrada na biblioteca da mesma. A biblioteca Time tambm
utiliza dessa lista, para verificar colises e afins.
`

export const example =
`
let canvasInstance = new gamid.graphics.Canvas(800, 800, 'purple');
let canvas = canvasInstance.element; //get <canvas> html attribute

// Cria um obejto do tipo quadrado com seus respectivos atributos.
// Width, height, posio no eixo X, posio no eixo Y, massa e cor, respectivamente.
let square = new gamid.graphics.Square(100, 100, 50, 75, 10, 'black');

// Adiciona a fsica de coliso de quadrado para o objeto criado anteriormente.
// Essa classe (SquareCollision) chama o mtodo que adiciona o objeto
// na lista collidableObjects (Como: graphics.collidableObjects.push(objeto)).
let squareCollision = new gamid.physics.SquareCollision(square);

// Adiciona o objeto criado na lista que ser utilizada pela classe Time,
// na qual o renderizar no canvas.
canvasInstance.addObject(square);

// A classe time chama alguns mtodos do collidableObjects
// Por exemplo: this.graphics.collidableObjects[0].resolveBorderCollision()
// Este mtodo em especfico adiciona a coliso de borda (Canvas)
// com os objetos que possuem fsica.

// Mais interaes que voc pode fazer com os collidableObjects
// esto descritos na pgina da fsica.
`



