export const title = "Controller"
export const links = [
                    {title:"Retornar", link:"/gamid-pages/controller"}
                    ]

export const content = 
`
Controller  a classe abstrata que decora objetos instanciados. Para 
extend-la,  necessrio escrever os mtodos OnKeyUp() e OnKeyDown(). 
Todas as outra funes no so necessrias para extenso, so apenas 
suportivas. Keyboard  uma classe que herda de controle, e serve como 
Classe base do Gamid para o controle. Alm dessas extenses, o usurio deve 
definir eventos para onKeyUp e eventos para onKeyDown serem disparados.
`

export const attributeList = [
    {attr: "addKey(key, func)", description: "Adiciona uma tecla  lista de teclas"},
    {attr: "setOnKeyUp(key, func)", description: "Modifica a funcionalidade de uma tecla j instanciada ao levant-la"},
    {attr: "setOnKeyDown(key, func)", description: "Modifica a funcionalidade de uma tecla j instanciada ao abaix-la"},
    {attr: "onKeyUp()", description: "Checa a lista de teclas, e caso alguma tecla registrada seja levantada, executa a funo correspondente"},
    {attr: "onKeyDown()", description: "Checa a lista de teclas, e caso alguma tecla registrada seja abaixada, executa a funo correspondente"},
]


