export const title = "Time"
export const links = []

export const content = 
`
Time  a nica biblioteca imutvel: Nada nela deve ser modificado, 
uma vez que ela  responsvel pelo funcionamento principal do framework.
Nela, objetos so atualizados e colises so verificadas. Nenhuma funo  modificvel.
`

export const attributeList = [
    {attr: "setTime()", description: "Funo que define a diferena de tempo entre um frame e outro"},
    {attr: "loop()", description: "Funo que capta a real diferena entre um frame e outro"},
    {attr: "updateGame(dt)", description: "Funo que recebe a real diferena entre dois frames, e realiza as atualizaes necessrias para o funcionamento da aplicao"}
     
]


