export const title = "Keyboard"
export const links = [
                    {title:"Retornar", link:"/gamid-pages/controller"}
                    ]

export const content =
`
Keyboard  o exemplo base do framework Gamid. Nele,  possvel atribuir teclas e
funcionalidades para cada uma dessas teclas, utilizando das funes herdadas de Controller.
Repara-se que a nica diferena entre Keyboard e Controller  que Keyboard j tem os eventos
definidos para um teclado.
`

export const attributeList = [
    {attr: "onKeyUp(event, functionList)", description: "Checa a lista de teclas, e caso alguma tecla registrada seja levantada, executa a funo correspondente"},
    {attr: "onKeyDown(event, functionList)", description: "Checa a lista de teclas, e caso alguma tecla registrada seja abaixada, executa a funo correspondente"}
]

export const example =
`
let canvasInstance = new gamid.graphics.Canvas(800, 800, 'purple');
let canvas = canvasInstance.element; // get <canvas> html attribute

// Cria um objeto do tipo Quadrado com seus respectivos atributos
let square = new gamid.graphics.Square(100, 100, 50, 75, 10, 'black');

// Adiciona um ou mais tipo de controles via Keyboard ao objeto instanciado (Square)
// Para reconhecer qual tecla ir ser utilizada, associa-se  sua keyCode:
// 87 = 'w' | 83 = 's'.
let keyboard = new gamid.controller.Keyboard(square, [87, 83]);

// Adiciona fsica de coliso de quadrado ao objeto criado.
let squareCollision = new gamid.physics.SquareCollision(square);

// Os mtodos abaixo representam as aes que as teclas devem tomar
// quando pressionadas ou quando forem desacionadas.

// O evento setOnKeyDown  acionado quando a tecla  pressionada.
// Podemos passar quaisquer funes para serem executadas.
// Neste caso, est mudando a velocidade do eixo Y do objeto 'square'.
keyboard.setOnKeyDown(87, function(){
		square.speedY = -500;
});

// O evento setOnKeyUp  acionado quando a tecla  solta.
// Podemos passar quaisquer funo para ser executada.
// Neste caso, est mudando a velocidade do eixo Y do objeto 'square', parando-o.
keyboard.setOnKeyUp(87, function(){
	square.speedY = 0;
});

keyboard.setOnKeyDown(83, function(){
		square.speedY = 500;
});

keyboard.setOnKeyUp(83, function(){
	square.speedY = 0;
});

// Adds the created object in the list that will be used by the time class.
canvasInstance.addObject(square);
`



